.components-musicList {
  width: 40%;
  margin: 0 auto;
  margin-top: 20px;
}
.components-musicList li {
  color: white;
  cursor: pointer;
  margin-bottom: 6px;
  list-style-type: none;
  font-size: 20px;
  position: relative;
}
.components-musicList li:hover .deleteSong {
  display: inline-block;
}
.components-musicList .playing {
  color: #c94444;
}
.components-musicList .deleteSong {
  display: none;
  position: absolute;
  right: 0;
  width: 20px;
  margin-left: 20px;
  color: #a83d3d;
}



