.components-progress {
  width: 100%;
  height: 3px;
  background: #ddd;
  cursor: pointer;
}
.components-progress .progress {
  height: 3px;
  position: relative;
}
.components-progress .progress::after {
  content: '';
  width: 3px;
  height: 17px;
  background: #ca4f4f;
  position: absolute;
  top: -7px;
  right: 0;
}



