.components-player {
  width: 700px;
  margin: auto;
  margin-top: 10px;
}
.components-player .toMisicList {
  font-size: 20px;
  margin-bottom: 20px;
}
.components-player a {
  color: #ec5959;
  text-decoration: none;
}
.components-player .music-title,
.components-player .music-artist {
  margin-bottom: 10px;
  color: white;
  text-align: center;
}
.components-player .smallContainer,
.components-player .control-container {
  position: relative;
}
.components-player .smallContainer {
  margin-bottom: 40px;
}
.components-player .left-time {
  display: block;
  color: white;
  width: 50px;
  height: 17px;
  font-size: 17px;
  margin: 20px auto;
}
.components-player .volumeContainer {
  position: absolute;
  right: 0px;
  top: 0;
  width: 200px;
}
.components-player .volumeContainer:hover .volumeWrapper {
  opacity: 1;
}
.components-player .volume-icon {
  display: inline-block;
  width: 50px;
  height: 10px;
  font-size: 15px;
  color: white;
}
.components-player .volumeWrapper {
  opacity: 0;
  width: 75%;
  display: inline-block;
  -webkit-transition: opacity .5s linear;
  -o-transition: opacity .5s linear;
  transition: opacity .5s linear;
}
.components-player .showCover {
  width: 400px;
  height: 400px;
  margin: 20px auto;
}
.components-player img {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  cursor: pointer;
  -webkit-animation: rotating 20s infinite linear;
          animation: rotating 20s infinite linear;
}
@-webkit-keyframes rotating {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotating {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.components-player .controlBtns {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  height: 65px;
  margin-top: 20px;
}
.components-player .controlBtns i {
  width: 64px;
  height: 64px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.components-player .play {
  background: url("../images/play.png");
  background-repeat: no-repeat;
}
.components-player .pause {
  background: url("../images/pause.png");
  background-repeat: no-repeat;
}
.components-player .next {
  background: url("../images/next.png");
  background-repeat: no-repeat;
}
.components-player .prev {
  background: url("../images/prev.png");
  background-repeat: no-repeat;
}
.components-player .playMode {
  position: absolute;
  bottom: 10px;
  right: -100px;
  cursor: pointer;
}
.components-player .shuffle {
  width: 35px;
  height: 35px;
  display: inline-block;
  background-image: url("../images/shuffle.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.components-player .circle {
  width: 35px;
  height: 35px;
  display: inline-block;
  background-image: url("../images/circle.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.components-player .repeat {
  width: 35px;
  height: 35px;
  display: inline-block;
  border-radius: 50%;
  background: white;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}



